import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController,Events } from 'ionic-angular';
import { InAppBrowser,InAppBrowserEvent,InAppBrowserOptions } from '@ionic-native/in-app-browser';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';


import { ImageViewerController } from 'ionic-img-viewer';
/**
 * Generated class for the BadgeBoardPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-badge-board',
  templateUrl: 'badge-board.html',
})
export class BadgeBoardPage {

  badgeMe:any;
  deviceId:any;
  login_token:any;
  employeeId:any;
  _imageViewerCtrl: ImageViewerController;
  badgeListData: any;myinfinte:any;
  value: any=0;
  badgeBoardErrorMessage:any='';
  leaderBoardErrorMessage:any=''
  leaderBoardListData: any;
  myinfinteLeaderBoard:any;
  leaderBoardSenderData:any;
  showleaderBoardReceiver:boolean=true;
  receiverRadioBTN: boolean;

  options : InAppBrowserOptions = {
    location : 'no',
    hidden : 'no',
    // clearcache : 'yes',
    // clearsessioncache : 'yes',
    zoom : 'yes',
    hardwareback : 'yes',
    mediaPlaybackRequiresUserAction : 'no',
    shouldPauseOnSuspend : 'no',
    closebuttoncaption : 'Back',
    closebuttoncolor:'#f04141',
    disallowoverscroll : 'no',
    toolbar : 'yes',
    enableViewportScale : 'no',
    allowInlineMediaPlayback : 'no',
    presentationstyle : 'pagesheet',
    fullscreen : 'yes',
    footer:'yes',
    footercolor:'#939399',
   };


  constructor(public events: Events,private storage:Storage,private apiServices:ApiServiceProvider,imageViewerCtrl: ImageViewerController,
    private alertCtrl: AlertController,public navCtrl: NavController, private iab: InAppBrowser, public navParams: NavParams) {
    this.badgeMe='BadgeBoard';

    this.storage.get('employeeId').then(data => {
      this.employeeId = data;
      console.log(' employeeId== ', this.employeeId);
    });
    this.storage.get('login_token').then((data) => {
      console.log('showOnBoard value is111==', data);
      this.login_token = data;
    });
    this._imageViewerCtrl = imageViewerCtrl;

    this.events.subscribe('totalCommentUpdate', (data) =>{
      console.log('totalCommentUpdate', data); // ðŸ‘‹ comming form comment page!
      this.badgeListData[data.index].comment_count=data.totalComment;
    });

    this.events.subscribe('updatelikeDataBadgeBoard', (data) =>{
      console.log('totalCommentUpdate', data); // ðŸ‘‹ comming form comment page!
      this.badgeListData[data.index].like_count=data.like;
      this.badgeListData[data.index].like_status=data.likeStatus;

      console.log('updatelikeData ds', this.badgeListData[data.index]); 



    });


    this.events.subscribe('updateCommentDataBadgeBoard', (data) =>{
      console.log('updateCommentDataBadgeBoard', data); // ðŸ‘‹ comming form comment page!
      this.badgeListData[data.index].comment_count=data.comment;
    });



  }

  LeaderBoardActive(){
    this.receiverRadioBTN = true;
    this.showleaderBoardReceiver=true;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NoticeListPage');
    this.getNoticeListFun();
    this.leaderBoardFun();
  }
  getNoticeListFun(){

    this.storage.get('employeeId').then(data => {
      this.employeeId = data;
      console.log(' employeeId== ', this.employeeId);
    });
    this.storage.get('login_token').then((data) => {
      console.log('showOnBoard value is111==', data);
      this.login_token = data;
    })
    this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      let apiKey = {
        "client_id":this.apiServices.clientId,
        "employee_id":this.employeeId,
        "device":this.apiServices.device,
        "device_id":this.deviceId,
        "app_version":this.apiServices.appVersion,
        "val":this.value
      };
      this.apiServices.badgeBoardApi(apiKey, this.login_token)
        .subscribe((res) => {
          console.log('notice List data==', res);
          if(res.success==1){
            if(this.badgeListData==undefined){
              this.badgeListData=res.data;
              this.badgeBoardErrorMessage='';
            }else{
              this.badgeListData= this.badgeListData.concat(res.data);
              this.myinfinte.complete();
              console.log('new data== ', this.badgeListData);
            }

          }else{
            if(this.badgeListData==undefined){
              this.badgeListData=[];
            }
            this.badgeBoardErrorMessage=res.message;
            this.myinfinte.complete();
            // let alert = this.alertCtrl.create({
            //   // title: 'Confirm purchase',
            //   message: res.message,
            //   buttons: [
            //     {
            //       text: 'Ok',
            //       role: 'cancel',
            //       handler: () => {
            //         console.log('Cancel clicked');
            //         this.navCtrl.pop();
            //       }
            //     }
            //   ]
            // });
            // alert.present();
          }


        })

      })



  }

  handleClick(event) {
    if (event.target.tagName == "A") {
      this.iab.create(event.target.href, "_system", this.options);
      return false;
    }
  }

  gotoSelectUser(){
    this.navCtrl.push('SelectedUserPage', {'nextModule':'badgeSend','awardstype':1});
  }


  presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
 
    setTimeout(() => imageViewer.dismiss(), 30000);
    imageViewer.onDidDismiss(() => console.log('Viewer dismissed'));
  }

  seeDetails(data){
    this.navCtrl.push('NoticeDetailsPage',{'data':data});
  }


  doInfinite(infiniteScroll) {
    this.myinfinte=infiniteScroll;
    console.log('Begin async operation');
    setTimeout(() => {
      this.value=this.badgeListData.length;
      this.getNoticeListFun();
     
    }, 500);
  }

  doInfiniteLeaderBoard(infiniteScroll) {
    this.myinfinteLeaderBoard=infiniteScroll;
    console.log('Begin async operation');
    setTimeout(() => {
      this.value=this.badgeListData.length;
      this.leaderBoardFun();
     
    }, 500);
  }

  leaderBoardFun(){
    this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      let apiKey = {
        "client_id":this.apiServices.clientId,
        "employee_id":this.employeeId,
        "device":this.apiServices.device,
        "device_id":this.deviceId,
        "app_version":this.apiServices.appVersion,
        "val":this.value
      };
      this.apiServices.leaderBoardApi(apiKey, this.login_token)
        .subscribe((res) => {
          console.log('leaderboard res==', res);
          if(res.success==1){
            if(this.leaderBoardListData==undefined){
              this.leaderBoardListData=res.receiver_leaderboard;
              this.leaderBoardSenderData=res.sender_leaderboard;
              console.log('leaderboard data== ', this.leaderBoardListData);
              console.log('sender data== ', this.leaderBoardSenderData);
              this.leaderBoardErrorMessage='';
              // this.myinfinteLeaderBoard.complete();
            }else{
              this.leaderBoardListData= this.leaderBoardListData.concat(res.receiver_leaderboard);
              this.leaderBoardSenderData= this.leaderBoardSenderData.concat(res.sender_leaderboard);
              this.myinfinteLeaderBoard.complete();
              console.log('new data== ', this.leaderBoardListData);
              console.log('sender data== ', this.leaderBoardSenderData);
            }
          }else{
            this.leaderBoardErrorMessage=res.message;
            this.myinfinteLeaderBoard.complete();
          }
        })
      })
  }

onItemChange(val){
  if(val==1){
    this.showleaderBoardReceiver=true;
  }else{
    this.showleaderBoardReceiver=false;
  }

}

seebadgeProfile(data,type){
  let alldata={
    'data':data,
    'badgeType':type
  }
  this.navCtrl.push('BadgeMeProfilePage', {'data':alldata});
}

likeUnlikeFun(data, index){
  console.log('post==', data);
if(data.like_status==0){
  this.badgeListData[index].like_status=1;
  let increaseLike=parseInt(data.like_count)+1;
  this.badgeListData[index].like_count=increaseLike;
  this.doLikeUnlikeFun(increaseLike,this.badgeListData[index].auto_id,1);
}else{
  this.badgeListData[index].like_status=0;
  let decreaseLike=parseInt(data.like_count)-1;
  this.badgeListData[index].like_count=decreaseLike;
  this.doLikeUnlikeFun(decreaseLike,this.badgeListData[index].auto_id,0);
}

}

doLikeUnlikeFun(totalLike,postId,status){
  this.storage.get('deviceId').then(data => {
    this.deviceId = data;
    let apiKey = {
      "client_id":this.apiServices.clientId,
      "employee_id":this.employeeId,
      "device":this.apiServices.device,
      "device_id":this.deviceId,
      "app_version":this.apiServices.appVersion,
      "flag":"10",
      "post_id":postId,
      "status":status
    };
    this.apiServices.doLikeUnlikeApi(apiKey, this.login_token)
      .subscribe((res) => {
        console.log('leaderboard res==', res);
        
        if(res.success==1){

        }else{

        }

      })
    })


}

seeComments(data,ind){
  this.navCtrl.push('CommentsPage',{'data':data, 'index':ind});
}


}



